function _para_1(md){return(
md`Probablistic record linkage is a technique used to link together records that lack unique identifiers.  

In the absence of a unique identifier such as a National Insurance number, we can use a combination of individually non-unique variables such as name, gender and date of birth to identify individuals.

Record linkage can be done within datasets (deduplication) or between datasets (linkage), or both.
`
)}

function _para_2(md){return(
md`Linkage is probabilistic in the sense that it relies on the balance on evidence.  In a large dataset, observing that two records match on the full name 'John Smith' provides some evidence that these two records may refer to the same person, but this evidence is inconclusive.  

More broadly, it is often impossible to classify pairs of records as matches or non-matches beyond any doubt.  Instead, the aim of probabilisitic record linkage to to quantify the probability that a pair of records refer to the same entity by considering evidence in favour and against a match and weighting it appropriately.
`
)}

function _para_3(md){return(
md`In probabilistic record linkage, we start with a prior, which represents the probability that two records drawn at random are a match.  We then compare the two records, increasing the match probability where columns agree, and decreasing it when columns disagree.  

`
)}

function _para_4(md){return(
md`The most common type of probabilistic record linkage model is called the Fellegi-Sunter model. 

Let's take a look at at an example of a trained Fellegi-Sunter model to calculate match probability interactively.  This model will compare the two records in the table, and assess whether they refer to the same person, or different people.`
)}

