function _para_7(md){return(
md`Columns with higher number of distinct values, such as date of birth, tend to represent stronger evidence in favour of a match since it's less likely that two records chosen at random would match by coincidence.  

Where a column does not match, this is usually evidence against a match.  However, the evidence may not be strong - for example, people move house, which could cause a mismatch on the 'postcode' column.

This, in a nutshell, is how probabilistic record linkage works.  By comparing records and weighting evidence appropriately, we estimate the probability of a match.`
)}

