function _tableData(Generators,parseTableData,table){return(
Generators.observe((notify) => {
  const keyinput = (event) => notify(parseTableData(table));
  table.addEventListener("input", keyinput, false);
  notify(parseTableData(table));
  return () => window.removeEventListener("input", keyinput);
})
)}

function _parseTableData(){return(
function parseTableData(table) {
  const data = [];

  table.querySelectorAll("tr").forEach((row, i) => {
    let cells = row.cells;
    let row_data = [];

    for (let cell of cells) {
      let value = cell.innerText;
      value = value.trim();
      value = value.toLowerCase();
      if (value == "") {
        value = null;
      }
      row_data.push(value);
    }
    data.push(row_data);
  });

  const formattedData = data.reduce((accumulator, row, i) => {
    if (i == 0) {
      accumulator.properties = row;
    } else {
      const rowObject = {};
      rowObject["uid"] = i;
      accumulator.properties.forEach((key, i) => {
        rowObject[key] = row[i];
      });
      accumulator.push(rowObject);
    }
    return accumulator;
  }, []);
  return formattedData;
}
)}

