function _fname_case_stmt(dm_intersect,distance){return(
function (row) {
  if (row.fname_l === null || row.fname_r === null) {
    return -1;
  }
  if (row.fname_l == row.fname_r) {
    return 3;
  }
  if (dm_intersect(row.fname_l, row.fname_r)) {
    return 2;
  }
  if (distance(row.fname_l, row.fname_r) > 0.85) {
    return 1;
  } else {
    return 0;
  }
}
)}

function _mname_case_stmt(dm_intersect,distance){return(
function (row) {
  if (row.mname_l === null || row.mname_r === null) {
    return -1;
  }
  if (row.mname_l == row.mname_r) {
    return 3;
  }
  if (dm_intersect(row.mname_l, row.mname_r)) {
    return 2;
  }
  if (distance(row.mname_l, row.mname_r) > 0.85) {
    return 1;
  } else {
    return 0;
  }
}
)}

