function _abs_date_diff_in_years(){return(
function (date_l, date_r) {
  let d_l = new Date(date_l);
  let d_r = new Date(date_r);
  let divisor = 24 * 60 * 60 * 1000 * 365;
  return Math.abs(d_l - d_r) / divisor;
}
)}

function _dob_case_stmt(levenshtein,abs_date_diff_in_years){return(
function (row) {
  if (row.dob_l === null || row.dob_r === null) {
    return -1;
  }
  if (row.dob_l == row.dob_r && row.dob_l.slice(-5) == "01-01") {
    return 4;
  }
  if (row.dob_l == row.dob_r) {
    return 5;
  }
  if (levenshtein(row.dob_l, row.dob_r) <= 1) {
    return 3;
  }
  if (abs_date_diff_in_years(row.dob_l, row.dob_r) < 1) {
    return 2;
  }
  if (abs_date_diff_in_years(row.dob_l, row.dob_r) < 10) {
    return 1;
  } else {
    return 0;
  }
}
)}

function _pc_case_stmt(){return(
function (row) {
  if (row.pc_l === null || row.pc_r === null) {
    return -1;
  }
  if (row.pc_l == row.pc_r) {
    return 5;
  }
  if (row.pc_l.slice(0, 3) == row.pc_r.slice(0, 3)) {
    return 4;
  } else {
    return 0;
  }
}
)}

