function _dm_intersect(dm){return(
function dm_intersect(val_l, val_r) {
  let arr_l = dm.doubleMetaphone(val_l);
  let arr_r = dm.doubleMetaphone(val_r);
  return arr_l.filter((value) => arr_r.includes(value)).length > 0;
}
)}

